<template>
  <div class="contain_wrap">
    <div class="return"></div>

    <div class="content">
      <div class="item" @click="ender">
        <div class="left">
          <van-image
            fit="cover"
            :src="'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3757239321,1175359126&fm=26&gp=0.jpg'"
          ></van-image>
        </div>
        <div class="right">啊啊</div>
      </div>
      <div class="list">
        <div class="item">
          <van-cell title="收藏" is-link @click="ender" />
        </div>
        <div class="item">
          <van-cell title="账户管理" is-link @click="ender" />
        </div>
      </div>
    </div>

    <FootTab></FootTab>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import FootTab from "../components/FootTab";
export default {
  data() {
    return {
      user: {},
      collect: []
    };
  },

  created() {},
  components: {
    FootTab
  },

  methods: {
    afterRead(file) {
      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );
      //获取图片类型
      let imgType = file.file.type.split("/")[1];
      let tokenString = localStorage.getItem("__Tk");
      let data = {
        tokenString,
        imgType,
        serverBase64Img
      };

      reaxios(1, { url: "/updateAvatar", method: "POST", data: data }).then(
        result => {
          this.$toast.clear();
          if (result.data.code == "H001") {
            this.userinfo.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
        }
      );
    },

    ender() {
      this.$toast("结束啦");
    }
  }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(243, 243, 243);
  .return {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 95%;
    background-color: var(--themeColor);
    text-align: left;
    font-size: 24px;
    display: flex;
    align-items: center;
    color: aliceblue;
  }
  .content {
    position: fixed;
    top: 40px;
    left: 10px;
    right: 10px;
    bottom: 50px;
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #fff;
      padding: 10px;
      margin: 10px 0;
      .left {
        flex: 1;
        text-align: left;
        .van-image {
          width: 30%;
          border-radius: 50%;
          overflow: hidden;
        }
      }
      .right {
        text-align: right;
        flex: 1;
      }
    }
    .list {
      .item {
        text-align: left;
        margin: 5px 0;
      }
    }
  }
}
</style>


